import { createAction } from "redux-actions";

const PRE_FIX = "DEMO_";
export const GETCODE = `${PRE_FIX}GETCODE`;
export const getCode = createAction(GETCODE, params => {
	return http
		.post({
			url: "get_code",
			params
		})
		.then(res => {
			if (res.code === 4000) {
				alert("发送成功");
			}
			return res;
		});
});
